:host {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    width: 100vw;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  }
  
  
  app-header,
  app-footer {
    width: 100%;
  }
  
  
  main {
    flex-grow: 1;
    padding: 0 80px;
    background-color: #fff;
  }
  